@import url(https://fonts.googleapis.com/css?family=Alike);
@import url(https://fonts.googleapis.com/css?family=Charm);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Alike';
    background: url(/static/media/cricket.72f53fc2.jpg);
    white-space: pre-wrap;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    display: table;
    top: 0;
    height: 100vh;
}
.app-nav-item {
    margin: 0 auto !important;
}

@media only screen and (max-width: 600px) {
    body {
        background-size: auto !important;   
      
     }
  }
.nav-navbar {
    font-family: 'Charm';
    font-weight: 600;
    background-color: #ffffffa8 !important;
}

.nav-link {
    text-align: center;
    margin-right: 3%;
    text-decoration: none;
}

a {
    text-decoration: none;
    color: #999;
    font-size: 20px;
    font-family: Droid Sans;
}


.mr-auto {
    margin-left: 60%;
}


.admin {
    margin-left: 20% !important;
}
.name {
    font-size: 26px;
    margin-left: 1%;
}

/* ----------- iPad Pro ----------- */
/* Portrait and Landscape */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (-webkit-min-device-pixel-ratio: 1.5) {     
.mr-auto {
    margin-left: 20%;
}
}
.form-container {
    margin: 4% auto !important;
}
.form-item {
    background: rgba(200, 200, 200, 0.4);
    padding-bottom: 40px;
    margin: 5% auto !important;
    border-radius: 1.5em;
    box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);
}

.form {
    text-align: center;
    padding: 30px;
    color: rgba(77, 22, 22, 0.8);
    font-weight: bold;
    font-size: 30px;
}

.form-form-item {
    margin: auto !important;
}

.form-form {
  
    margin: 0 auto;
    text-align: center;
}

.form-credential {
    width: 100%;
    color: rgb(38, 50, 56);
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 1px;
    background: rgba(255, 255, 255, 0.5);
    padding: 10px 20px;
    border-radius: 20px;
    outline: none;
    box-sizing: border-box;
    border: 2px solid rgba(0, 0, 0, 0.2);
    margin-bottom: 30px;
    text-align: center;
}

.form-credential:focus {
    border: 2px solid rgba(0, 0, 0, 0) !important;
}

.form-button {
    padding: 4%;
    font-weight: 700 !important;
    font-size: 20px !important;
    border-radius: 5em !important;
    color: #fff !important;
    background: linear-gradient(to right, rgba(77, 22, 22, 0.8), rgba(228, 16, 16, 0.2)) !important;
    border: 0 !important;
    box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.04) !important;
    text-shadow: 0px 0px 3px rgba(117, 117, 117, 0.12) !important;
}

.form-control {
    resize: none;
}
 .form-character {
     font-size: 20px;
 }
.form-label {
    font-size: 20px;
    font-weight: 600;
}

.contact-container {
    margin: 10% auto !important;
}

.contact-card {
    padding: 4%;
    text-align: center;
}

.contact-details {
    font-size: 30px;
    margin-bottom: 4%;
    font-family: inherit;
}

.detail-key .detail-value {
    font-family: inherit;
    font-size: 20px;
}

.detail-key {
    margin-bottom: 2% !important;
}

.card {
    border: 8px solid rgba(160, 147, 147, 0.34);
}

.card-body {
    background: #eef1f5b8;
}
.feed-item {
    margin: 0 auto !important;
}

.feed-card {
    margin: 10% 0;
    max-width: 800px;
}

.feed-container {
    background-color: #eef7ffde;
}
.cross {
    width: 40px;
    height: 40px;
}

.img-box {
  margin-right: 1em;
}
.edit-table {
    margin: auto !important;
    margin-top: 5% !important;
    max-width: 1200px !important;
}

.icon-cell {
   text-align: center !important;
    border-radius: 20%;
}

.title-cell {
    font-family: inherit !important;
    font-size: 18px !important;
    font-weight: 600 !important;
    text-align: center !important;
    color:  white !important;
}

.report-cell {
    font-family: inherit !important;
    font-size: 14px !important;
    font-weight: 600 !important;
    color:  white !important;
}

.icon-div {
    position: relative;
    background: #fbfdffd6;
    padding: 20%;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    text-align: center;
}

.icon {
    position: absolute;
   transform: translate(-10px, -10px);
}

.table-container {
    background: #0d0e19e0;
    padding: 5%;
    border-radius: 5%;
}

.edit-your-blog {
    text-align: center;
    margin-bottom: 2%;
    font-size: 30px;
    font-weight: 600;
    color: black;
}

.heading {
    font-family: inherit !important;
    font-size: 18px !important;
    font-weight: 600 !important;
    color:  white !important;
}

.icon-del {
    position: absolute;
    transform: translate(-10px, -10px);
    top: 50%;

}
@media only screen and (max-width: 600px) {
    .icon {
        transform: translate(-11px, 0px);
    }
  }
  

